
<script>
import LeadDataService from "../../../services/DataService";
import DynamicForm from "../../../components/Common/DynamicForm.vue";
import modaladdindustry from '../../../components/Common/Modal/modaladdindustry.vue'
export default {
    components: {
        DynamicForm,
        modaladdindustry
    },
    data() {
        return {
            isLoading:false,
            value: "",
            LeadData: null,
            LeadOwner: null,
            GroupBy: [],
            Channel: null,
            IndustryType: null,
            Status: null,
            ApiURL: `moduleName=CRM&subModuleCode=CRM_ACCOUNTS&langCode=en`,
            options: null,
            selected: false,
            selectoptions: [],
            submited: false,
            IscheckContact: false,
            showaddindustryModal: false,
            IndustryItemObject: null,
            cityItemObject: null,
            showaddcityModal: false,
            txtfirstname: '',
            txtlastname: '',
            txtemailid: '',
            txtmobile: '',
            accountID: this.$route.params.id,
            divchkhide: false,
            CountryData: null,
            phoneList: [],
            name: null,
            Id: null,
            dateformat: null,
            phoneError:false
        }
    },
    created: function () {debugger;
        this.name = this.GetUserInfo.Name;
        this.Id = this.GetUserInfo.ID;
        this.dateformat = this.GetUserInfo.DateFormat;
        var vm = this;
        var reqid = this.$route.params.id,
            reqid = reqid == "" ? 0 : reqid;
        if (reqid == 0)
            vm.divchkhide = true;
        vm.FetchData();
    },
    methods: {
        onInputMobilePhone(phone, phoneObject, input) {
            debugger
            if(phone){
                if(phoneObject && phoneObject?.formatted.length >0 && (!phoneObject?.valid || phoneObject?.valid==undefined)){
                 this.phoneError=true;
            }else
             if (phoneObject?.formatted && phoneObject?.valid) {
                 phone=phoneObject.formatted;
                 this.fieldInfo.value = phoneObject.formatted;
                 this.phoneError=false;
             }
    }
        },
        onCancel: function () {
            this.$router.push({ name: 'CRMAccounts' });
        },
        IsPaste: function (evt, name) {
            var nameArray = ["commission_percentage", "txtmobile", "annual_revenue"]
            if (nameArray.indexOf(name) > -1) {
                if (!IsNumeric(evt.clipboardData.getData('text'))) {
                    evt.preventDefault();
                }
            }
        },
        isNumber: function (evt, name) {
            var nameArray = ["commission_percentage", "txtmobile"]
         if (nameArray.indexOf(name) > -1) {
                var charCode = (evt.which) ? evt.which : evt.keyCode;
                if (((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) || evt.currentTarget.value.length > 15) {
                    evt.preventDefault();
                }
                else {
                    return true;
                }
            }
        },           
        ChkContactbox: function (e) {
                var vm=this;
                vm.IscheckContact=$(e.target).prop("checked");
                if (!vm.IscheckContact) {
                    vm.txtfirstname = ''
                    vm.txtlastname = '',
                    vm.txtemailid = '',
                    vm.txtmobile = ''
            }
            if (vm.IscheckContact) {
                var reqid = vm.$route.params.id,
                    reqid = reqid == "" ? 0 : reqid;
               
            }
        },
        onKeyDown1: function () {
            ajaxRequestComplete = true;
        },
        FillDropdown: function (item, action) {            
            var vm = this;
            var refId = '';
            var search = '';
            var typeCode = '';
            if (item.name != "status_id") {
                if (item.name == 'account_country') {
                    typeCode = 'ACCOUNT_STATE';
                }
                else {
                    typeCode = '';
                }
                var itemname = item.name;
                refId = item.value;
                if (refId != '') {
                    var url = `id=&moduleName=CRM&type=${typeCode}&refId=${refId}`;
                    LeadDataService.getDDLData(url + "&search=" + escape(search)).then(function (response) {
                        var json = [];
                        if (response.data != null) {
                            json = response.data.DATA;
                        }
                        $.each(vm.GroupBy, function (index, item) {
                            $.each(item.Data, function (index, element) {
                                if (element.name == 'account_state' && itemname == 'account_country') {
                                    element.select_options = json;
                                }
                                else if (element.name == 'account_city' && itemname == 'account_state') {
                                    element.select_options = json;
                                }
                            });
                        });

                    });
                }
            }
            else if (item.name == "status_id") {
                $.each(this.GroupBy, function (index, itm) {
                    $.each(itm.Data, function (index, element) {
                        if ((element.name == 'account_state' || element.name == 'account_city')) {
                            element.is_required = false;
                        }
                    });
                });
            }
        },
        openaddIndustrypopup: function (item) {
            this.IndustryItemObject = item;
            this.showaddindustryModal = true;
        },
        openaddcitypopup: function (item) {
            this.cityItemObject = item;
            this.showaddcityModal = true;
        },
        GetValidation: function (object) {
            var obj = {};
            if (object.is_required) {
                obj['required'] = true;
            }
            if (object.length > 0) {
                obj['max'] = object.length;
            }
            if (object.validation_type == "email") {
                obj['email'] = true;
            }
            if (object.dt_code == "date") {
                obj['date_format'] = String(Globalize.culture().calendar.patterns.d).toUpperCase();
            }
            if (object.dt_code == "decimal") {
                obj['decimal'] = 2;
            }
            if (object.dt_code == "int" || object.dt_code == "bigint") {
                obj['numeric'] = true;
            }
            if (object.dt_code == "url") {
                obj['url'] = 'require_protocol';
            }
            if (object.name == "commission_percentage") {
                obj['min_value'] = 0; obj['max_value'] = 100; obj['decimal'] = 2;
            }
            if (object.name == "account_zip_code") {
                obj['numeric'] = true;
                obj['min'] = 5;
                obj['max'] = 15;
            }
            if (object.name == "employees") {
                obj['numeric'] = true;
            }
            if (object.name == "account_city") {
                obj['regex'] = /^[a-zA-Z ]*$/;
            }
            if (object.name == "website") {
                obj['regex'] = /^(https?: \/\/(?:www\.|(?!www))[a-zA-Z0-9][a-zA-Z0-9-]+[a-zA-Z0-9]\.[^\s]{2,}|www\.[a-zA-Z0-9][a-zA-Z0-9-]+[a-zA-Z0-9]\.[^\s]{2,}|https?:\/\/(?:www\.|(?!www))[a-zA-Z0-9]+\.[^\s]{2,}|www\.[a-zA-Z0-9]+\.[^\s]{2,})*$/
            }
            return obj;
        },
        SubmitData(values) {
            debugger;
          var vm=this;
            // this.$refs.form.setFieldTouched('Email', true);
            // this.$refs.form.setFieldTouched('LastName', true);
            // this.$refs.form.setFieldTouched('FirstName', true);             
            // this.LeadData.forEach((item) => {
            //     if (item.astype != '' && item.form_field_visibility == 'YES') {
            //         if (item.dt_code == "select" && item.value) {
            //             try {
            //                 values[item.name] = String((typeof item.value === 'object' && item.value !== null) ? item.value.value : item.value);
            //                 vm.$refs.form.setFieldTouched(item.name, false);
            //             } catch (ex) { console.log('error while get dropdown parse data') }
            //         }
            //         if (item.dt_code == 'date' && item.value) {
            //             values[item.name] =vm.$options.filters.formatDate(item.value);
            //             vm.$refs.form.setFieldTouched(item.name, false);
            //         } else {
            //             vm.$refs.form.setFieldTouched(item.name, true);
            //         }                 
            //     }
            // });            
            var Cname = "";
            var adrs1 = "";
            var adrs2 = "";
            var cty = "";
            var zipc = "";
            var accname = "";
            vm.$refs.form.validate().then((result) => {
                vm.vSelectValidate = true;
                if (result) {
                    vm.isLoading=true;
                    var fullJson = vm.LeadData;
                    var data = [];
                    var validation = [];
                    var accountcontact = [];

                    var Isvalidcnt = 0;
                    $.each(fullJson, function (key, item) {
                        var validationObject = {
                            field_name: item.name,
                            required: item.is_required,
                            length: item.length,
                            type: item.validation_type
                        };
                        if (item.name == "account_name") {
                            accname = item.value;
                        }
                        if (item.name == "account_owner") {
                            item.value = values[item.name];
                        }
                        if (item.name == "company_name")
                            Cname = item.value;
                        if (item.name == "account_address")
                            adrs1 = item.value;
                        if (item.name == "account_address2")
                            adrs2 = item.value;
                        if (item.name == "account_city")
                            cty = item.value;
                        if (item.name == "account_zip_code")
                            zipc = item.value;
                        if (item.name == "phone") {
                            var datalist = vm.phoneList.filter(v => v.field_id == item.custom_field_id);
                            if (datalist.length > 0) {
                                item.value = datalist[0].value;
                                if (!datalist[0].isValid && $.trim(item.value).length > 0) {
                                    $("#spanPhone").show();
                                    Isvalidcnt++;
                                }
                            }
                        }
                        var fieldValue = (item.name == "account_id" ? vm.accountID : item.value);
                        if (item.dt_code == "date") {
                            fieldValue = item.value;
                        }
                        if (item.dt_code == "checkbox") {
                            var filtered = item.select_value.filter(function (el) {
                                return (el != null && el != "");
                            });
                            fieldValue = filtered.join(',');
                        }
                        if (item.dt_code == "select") {
                            try {
                                fieldValue = (typeof fieldValue === 'object' && fieldValue !== null) ? fieldValue.value : fieldValue
                            } catch (ex) { }
                        }
                        var dataObject = {
                            field_name: item.name,
                            field_value: fieldValue,
                            custom_field_id: item.custom_field_id
                        };
                        data.push(dataObject);
                        validation.push(validationObject);
                        accountcontact.push(accountcontactobject);
                    });
                    const urlParams = new URLSearchParams(window.location.search);
                    var IdExist = urlParams.get('id') || 0;
                    var AccType = urlParams.get('AccountCode') || "";
                    if (IdExist == null) {
                        IdExist = 0;
                    }
                    var stateName = $('select[name="account_state"] option:selected').text();
                    var companyaddr = Cname + " " + adrs1 + " " + adrs2 + " " + cty + " " + (stateName == "Select" ? "--" : stateName) + " " + zipc;
                    var contactaddr = vm.txtfirstname + " " + vm.txtlastname + " " + adrs1 + " " + adrs2 + " " + cty + " " + (stateName == "Select" ? "--" : stateName) + " " + zipc;
                    if (IdExist == 0 && accname == '')  
                    {
                        var newAddres = Cname != '' ? $.trim(companyaddr) : $.trim(contactaddr);
                        var previousItem = data[data.findIndex(x => x.field_name == "account_name")];
                        previousItem.field_value = newAddres;
                    }
                    if (vm.IscheckContact) {
                        var MobPhone = vm.phoneList.filter(v => v.field_id == 'txtmobile');
                        if (MobPhone.length > 0) {
                            vm.txtmobile = MobPhone[0].value;
                            if (!MobPhone[0].isValid) {
                                $("#spantxtmobile").show();
                                Isvalidcnt++;
                            }
                        }
                    }
                    if (Isvalidcnt > 0) {

                        return false;
                    }
                    var accountcontactobject = {
                        first_name: vm.txtfirstname,
                        last_name: vm.txtlastname,
                        email_id: vm.txtemailid,
                        mobile_number: vm.txtmobile
                    };
                    var postJSON = null;
                    if (vm.IscheckContact) {
                        postJSON = {
                            data: data,
                            validation: validation,
                            accountcontact: accountcontactobject
                        };
                    }
                    else {
                        postJSON = {
                            data: data,
                            validation: validation
                        };
                    }
                    var objectPostString = JSON.stringify(postJSON);
                    LeadDataService.SaveAccountInfo(objectPostString).then(function (response) {
                        vm.isLoading=false;
                        if (response.data[0].Code == "1") {
                            if (vm.accountID > 0) {
                                vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Account')]), "success", true, vm.$t("Alert"));

                            } else {
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Account')]), "success", true, vm.$t("Alert"));
                            }
                            vm.$router.push({ name: 'CRMAccounts' });
                        }

                        else if (response.data[0].Code == "2") {
                            vm.ShowAlert(vm.$t("Account name already exist"), "warning", true, vm.$t("Alert"));
                        }
                        else {
                            vm.ShowAlert(vm.$t('AddedError', [vm.$t('Account')]), "failure", true, vm.$t("Alert"));
                        }

                    });
                }
            });
        },
        UpdateSelectJson: function (name, json) {
            $.each(this.GroupBy, function (index, item) {
                $.each(item.Data, function (index, element) {
                    if (element.name == name) {
                        element.select_options = json;
                    }
                });
            });
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading=true;
            var AccountID = vm.accountID == "0" ? "" : vm.accountID;
            var otherData = [{ id: vm.$route.params.AccountId, name: vm.$route.params.AccountCode }];
            var convertJson = encodeURIComponent(JSON.stringify(otherData))
            LeadDataService.getFormLayout(vm.ApiURL + "&id=" + AccountID + "&otherData=" + convertJson)
                .then(function (response) {
                    var IsActive = true;
                    var Compphone = '';
                    vm.isLoading=false;
                    vm.LeadData = response.data.data;
                    if (vm.LeadData != null && vm.LeadData.length > 0) {
                        var groupId = vm.LeadData[0].group_id;
                        $.each(vm.LeadData, $.proxy(function (index, item) {
                            groupId = item.group_id;
                            const urlParams = new URLSearchParams(window.location.search);
                            var IdExist = urlParams.get('id') || 0;
                            var AccType = urlParams.get('AccountCode') || "";
                            if (item.name == 'industry_name') {
                                item.isPlusIcon = true;
                            }
                            if (IdExist == null) {
                                IdExist = 0;
                            }
                            if (item.dt_code == 'date') {
                                item.value = item.value == '' ? null : item.value;
                            }
                            if (IdExist == 0 && AccType != "" && item.name == "account_name") {
                                item.is_required = true;
                            }
                            if (item.dt_code == 'checkbox') {
                                item.select_value = item.value.split(',');
                            }
                            if (item.name == 'account_type' && vm.accountID == 0) {
                                item.value = vm.$route.params.AccountId
                            }

                            if (item.name == 'account_owner') {
                                if (item.value == "") {
                                    item.select_value = vm.name;
                                    item.value = {name:item.select_value,value:vm.Id,first_value: null, second_value: null};                                    
                                    item.select_options=[{name:item.select_value,value:vm.Id,first_value: null, second_value: null}]
                                }
                                else {
                                    item.select_options =vm.MakeNormalArray(item.select_options);
                                }
                            }
                            if (item.name == "status_id") {
                                IsActive = item.value;
                            }
                            if (item.name == 'account_state' || item.name == "account_city") {
                                item.is_required = false;
                                item.select_options = null;
                            }
                            if (item.name == 'account_country') {
                                vm.CountryData = item;
                            }
                            
                            if (item.name == 'account_country' && vm.accountID > 0) {
                                if (item.value == '') {
                                    var cntrydata = JSON.parse(vm.CountryData.select_options);
                                    $.each(cntrydata, function (index, element) {
                                        if (element.name == "United States")
                                            vm.CountryData.value = element.value;
                                    });
                                    vm.FillDropdown(vm.CountryData);
                                }
                                else {
                                    vm.FillDropdown(item, 'Edit');
                                }
                            }
                            if (item.name == 'account_state' && vm.accountID > 0) {
                                vm.FillDropdown(item, 'Edit');
                            }
                            if (item.name == "phone") {
                                Compphone = item.value;
                            }
                            var chkGroup = $.grep(vm.GroupBy, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(vm.LeadData, function (element, index) {
                                    return element.group_id == groupId;
                                });
                                if (vm.accountID > 0) {
                                    $.each(returnedData, function (filedIndex, fieldItem) {
                                        if (fieldItem.dropdown_type == "custom" && fieldItem.picklist_options == "Lookup") {
                                            fieldItem.select_options = vm.MakeNormalArray(fieldItem.select_options);
                                            fieldItem.select_value = fieldItem.select_options;
                                            if (fieldItem.select_options != null && fieldItem.select_options.length > 0) {
                                                fieldItem.value = fieldItem.select_options[0];
                                            }
                                        }
                                        else if (fieldItem.dt_code == "decimal" && fieldItem.form_field_visibility == "YES" && fieldItem.value > 0) {
                                            fieldItem.value = parseFloat(fieldItem.value, 10);
                                        }
                                        else if (fieldItem.dt_code == "date") {
                                            fieldItem.value = fieldItem.value;
                                        }
                                    });
                                }
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        group_display_name: returnedData[0].group_display_name,
                                        Data: returnedData
                                    };
                                    vm.GroupBy.push(objGroup);
                                }
                            }
                        }, vm));
                        setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                        setTimeout(function () {                            
                            var cntrydata = JSON.parse(vm.CountryData.select_options);
                            if (AccountID == "") {
                                $.each(cntrydata, function (index, item) {
                                    if (item.name == "United States")
                                        vm.CountryData.value = item.value;
                                });
                                vm.FillDropdown(vm.CountryData);                               
                            }
                        }, 300);
                        var frmFields={}
                        vm.LeadData.forEach((item)=>{
                             if(item.astype!='' && item.form_field_visibility == 'YES'){                               
                             frmFields[item.name]=item.value;
                             }
                         })
                        //  vm.$refs.form.resetForm({values:frmFields});
                    }
                });
        },
        onSearch: function onSearch(search, objItem,selctedValue) { 
            this.searchSelectData(search, this, objItem,selctedValue);       
        },
        searchSelectData: function (search, vm, objItem,selctedValue) {
            this.ShowSearchData(search, vm, objItem,selctedValue);
        },
        ShowSearchData(search, vm, objItem,selctedValue) {
            if (typeof (selctedValue) == 'undefined') {
                    selctedValue = 0;
                }
            var url = "id=&moduleName=CRM"+"&type="+objItem.field_code;
            if (objItem.dependent_type != 'fill') {
                    url += "&refId=" + selctedValue;
                }
                LeadDataService.getDDLData(url + "&search=" + escape(search))
                .then(response => {
                    var json = [];
                    if(response.data==''){                       
                    }
                    if (response.data != '') {
                        json = response.data;
                    } 
                        objItem.select_options = json.DATA;                       
                        return objItem.select_options;           
                });
        },
        setSelectedValue: function (item, val) { 
                var vm = this;
                try {
                    if (Array.isArray(val)) {
                        item.value = val[0];
                    }
                    else {
                        if (item.name == 'account_owner') {
                            vm.ShowSearchData('', vm, item, '');
                        }
                        else {
                          if (item.has_dependent > 0) {
                                $.each(vm.GroupBy, $.proxy(function (key, parentItem) {
                                    $.each(parentItem.Data, function (childKey, childItem) {
                                        if (childItem.parent_id == item.custom_field_id && childItem.dependent_type == 'fill') {
                                            vm.ShowSearchData('', vm, childItem, val.value);
                                        }
                                    });
                                }, this));
                            }
                        }
                        item.value = val;
                    }
                }
                catch (ex) {
                }
            },
    }
}
</script>
<template>

   <section class="main-content">
     
        <div id="divMainLeadData" v-cloak>
            <loader :is-visible="isLoading"></loader>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('Account')}} {{$t('Information')}}</span>
                        <span class="p-actions float-right">
                            <a href="javascript:;" class="p-action-btn text-white" :title="$t('PageTour')"
                            @click="setTourGuideSteps(0)"><em class="fa fa-road"
                                    aria-hidden="true"></em><br>{{$t('PageTour')}}</a>
                            <RouterLink :to="'/CRM/Accounts'" title="" id="backtolist"
                                class="p-action-btn text-white float-right" :data-original-title="$t('BacktoList')"><em class="fa fa-angle-double-left pr-2"></em><br />{{ $t('BacktoList') }}</RouterLink>
                        </span>
                    </div>
                </div>
            </div>
            <div class="border p-3">
                <Form v-slot="{ valid,errors}" ref="form">
                    <DynamicForm :schema="GroupBy" :dateformat="dateformat" v-on:openaddIndustrypopup="openaddIndustrypopup"
                    v-on:FillDropdown="FillDropdown" v-on:onSearchDDL="onSearch"  v-on:setSelectedValue="setSelectedValue" subModuleCode="ManageAccount" />
                    <div class="row" v-if="divchkhide">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left my-2">
                            <div class="form-group">
                                <div class="float-left">
                                    <input id="chkcontactinfo" class="ml-3" value="1" 
                                        type="checkbox" v-on:change="ChkContactbox($event)">
                                </div>
                                <div class="float-left pt-1">
                                    <span style="font-weight: 500;">Tick to add Contact.</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row" v-if="IscheckContact">
                        <div class="col-lg-12 mt-4">
                            <div class="heading-border mb-2">
                                <h5 class="h5">
                                    <span>{{$t('ContactInformation')}}</span>
                                </h5>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                            <div class="form-group">
                                <label>{{$t('FirstName')}}:<span class="text-danger">*</span></label>
                                <Field name="FirstName" :rules="{'required':true}" label="First Name"
                                    v-slot="{errors,touched,invalid}">
                                    <input type="text" v-model="txtfirstname"
                                        v-bind:class="{'form-control' : true,'is-invalid': touched && invalid}"
                                        name="FirstName" maxlength="100">
                                    <span   name="FirstName"
                                        class="invalid-feedback">{{errors[0]}}</span>
                                </Field>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                            <div class="form-group">
                                <label>{{$t('LastName')}}:<span class="text-danger">*</span></label>
                                <Field name="LastName" :rules="{'required':true}" label="Last Name"
                                    v-slot="{errors,touched,invalid}">
                                    <input type="text" v-model="txtlastname"
                                        v-bind:class="{'form-control' : true,'is-invalid': touched && invalid }"
                                        name="LastName" maxlength="100">
                                    <span v-if="touched && invalid " name="LastName"
                                        class="invalid-feedback">{{errors[0]}}</span>
                                </Field>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                            <div class="form-group">
                                <label>{{$t('Email')}}:<span class="text-danger">*</span></label>
                                <Field name="Email" :rules="{'required':true,'email':true}" label="Email"
                                    v-slot="{errors,touched,invalid}">
                                    <input type="text" v-model="txtemailid" id="Emailid" name="Email"
                                        v-bind:class="{'form-control':true,'is-invalid': touched && invalid}">
                                    <span   name="Email" class="invalid-feedback">{{ errors[0] }}</span>
                                </Field>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                            <div class="form-group">
                                <label>{{$t('Mobile')}}: </label>
                                <vue-tel-input id="txtmobile" mode="international" @input="onInputMobilePhone" v-model="txtmobile" name="txtmobile"></vue-tel-input>
                                <span   :name="txtmobile" class="invalid-feedback d-block" >{{errors[0]}}</span>
                                <span v-if="phoneError"  class="invalid-feedback d-block" >Enter valid number.</span>
                            </div>
                        </div>
                    </div>
            <div class="row flex-row-reverse mt-2">
              <div class="col-lg-6 text-right">
                <a href="javascript:;"               
                  v-bind:class="{ disabled: false }"
                  v-on:click="SubmitData(valid,errors)"
                   class="btn btn-success formbtn"
                  title="Save"
                >
                  <em class="fa fa-save pr-2"></em>{{$t('Save')}}
                </a>&nbsp;
                <a href="javascript:void(0)" class="btn btn-danger" title="Cancel" @click="onCancel">
                  <em class="fa fa-close pr-2"></em>{{$t('Cancel')}}
                </a>                
              </div>
              <div class="col-lg-6">
                <div class="py-2">
                 <small class="text-danger">{{$t('MandatoryString')}}</small>
                </div>
              </div>
            </div>   
            <modaladdindustry v-if="showaddindustryModal" @close="showaddindustryModal=false" @updatedata="UpdateSelectJson"></modaladdindustry>
          </Form>
        </div>
    </div>
    </section>

</template>